'use client'
import styles from "../../page.module.css";
import Form from 'react-bootstrap/Form';
import EntryBox from "../../_components/entrybox";
import Submit from "../../_components/submit";
import Select from "../../_components/select";
import { useEffect, useReducer } from "react";
import CheckBox from "../../_components/checkbox";
import { fetch } from "../../_scripts/communications";
import backgroundImage from "../../../public/Green.png"
import ButtonEntry from "@/app/_components/buttonentry";
import Feedback from "@/app/_components/feedback";
import FormTitle from "@/app/_components/formtitle";

let submitObject = {};

const initState = {
    'LSManager Account':{
        value:''
    },
    'Company Code':{
        value:''
    },
    'Implementation End':{
        value:'2023-01-01'
    },
    'Submit':{
        Color:'primary',
        Text:'Submit'
    },
    'Load Account':{
        value:''
    },
    'Feedback':{
        color:'success',
        code: 200,
        title:'Well Done',
        text:'This is some sample text',
        hidden:false
    },
    'Feedback':{
        color:'success',
        code:'',
        title:'',
        text:'',
        hidden: true
    }
};


const reducer = (state, action)=>{

    let newState = {...state};

    switch (action.type){

        case 'update-Value':{

            newState[action.label].value = action.value;

            newState.Submit.Color = 'primary';
            newState.Submit.Text = 'Submit';

            if(action.label == 'Load Account'){

                newState['Company Code'].value = '';
                newState['Implementation End'].value = '';
                newState['Feedback'].hidden = true;

            }

            return newState

            break
        }

        case 'submit-Clicked':{

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Error':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Success':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;

        }
        
        case 'mark-Checked':{

            newState[action.label].isChecked = action.value;

            return newState

            break;

        }
        case 'load-Options':{

            //console.dir(action.data);

            let account = action.data.data.account[0];

            newState['Company Code'].value = account.Company_Code;

            newState['Implementation End'].value = account.Implementation_Expiration;

            return newState

            break;
        }
        case 'load-Feedback':{

            newState['Feedback'].hidden = false;
            newState['Feedback'].color = action.color;
            newState['Feedback'].code = action.code;
            newState['Feedback'].title = action.title;
            newState['Feedback'].text = action.text;

            return newState;

            break;

        }

    }
};

export default function UpdateAccount(){


    const [state, dispatch] = useReducer(reducer,initState);

        
    submitObject.Company_Code = state['Company Code'].value
    submitObject.LSManager_Account = state['Load Account'].value
    submitObject.Implementation_Expiration = state['Implementation End'].value

    return (

        <main className={styles.main} style={{backgroundImage: `url(${backgroundImage.src})`}}>
            
          <Form>
            <FormTitle text='Update Account'></FormTitle>
            <ButtonEntry
                label="Load Account"
                type="numeric"
                placeholder="LS Manager Account"
                value={state['Load Account'].value}
                dispatch={dispatch}
                data={{'LSManager_Account':state['Load Account'].value}}
                endpoint="GetAccountInfo"
            >
            </ButtonEntry>
            <EntryBox
                label='Company Code'
                type = 'text'
                reminder =''
                placeholder = ''
                hidden = {false}
                value = {state['Company Code'].value}
                dispatch={dispatch}
            ></EntryBox>
            <EntryBox
                label='Implementation End'
                type = 'date'
                reminder =''
                placeholder = ''
                hidden = {false}
                value = {state['Implementation End'].value}
                dispatch={dispatch}
            ></EntryBox>
            <Submit 
                method="POST"
                endpoint="UpdateAccount"
                dispatch={dispatch}
                data={submitObject}
                color={state.Submit.Color}
                text={state.Submit.Text}
            ></Submit>
            <Feedback
                color={state['Feedback'].color}
                code={state['Feedback'].code}
                title={state['Feedback'].title}
                text={state['Feedback'].text}
                hidden={state['Feedback'].hidden}
            ></Feedback>
          </Form>
        </main>
    );
}